body {
  background-color: #f6f6f6;
  font-family: Arial, sans-serif;
  color: #000000; }

button,
a {
  cursor: pointer; }

.card {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e2e2e2;
  overflow: hidden; }
  .card:hover .card__overlay {
    opacity: 1;
    transition: opacity 0.1666s ease; }

.card__image {
  width: 100%;
  height: 360px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.card__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 360px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 1;
  opacity: 0;
  transition: opacity 0.1666s ease; }
  .card__overlay .like-count {
    color: #ffffff;
    text-align: center;
    margin-top: 10px;
    font-size: 22px; }
  @media (max-width: 768px) {
    .card__overlay {
      position: static;
      top: initial;
      left: initial;
      height: initial;
      background: none;
      z-index: 0;
      opacity: 1; }
      .card__overlay .like-count {
        color: #c6c6c6;
        margin-top: 0; } }

.card__overlay-actions {
  float: right;
  margin-top: 20px;
  margin-right: 20px; }
  .card__overlay-actions .like-button {
    transition: opacity 0.3s ease; }
    .card__overlay-actions .like-button:hover {
      opacity: 0.6;
      transition: opacity 0.3s ease; }
  @media (max-width: 768px) {
    .card__overlay-actions {
      float: none;
      margin-top: 0;
      margin-right: 0; } }

@media (max-width: 768px) {
  .card__action {
    height: 60px;
    width: 50%;
    border-top: 1px solid #e2e2e2;
    border-right: 1px solid #e2e2e2;
    display: flex;
    align-items: center;
    justify-content: center; }
    .card__action button img {
      filter: brightness(0.7); }
    .card__action button.like-button--active img {
      filter: brightness(1); }
    .card__action:first-of-type button {
      margin-left: 20px; }
    .card__action:last-of-type {
      border-right: none; }
      .card__action:last-of-type button {
        margin-right: 20px; } }

.card__content {
  height: 100px;
  padding: 0 20px; }

.card__title {
  font-family: "Neutraface";
  font-size: 30px;
  text-transform: uppercase;
  margin-top: 25px;
  text-align: center; }

.card__author {
  margin-top: 10px;
  font-family: "Droid Serif Regular";
  text-align: center;
  font-size: 19px; }

.header {
  background-color: #ffffff;
  height: 180px;
  overflow: hidden; }

.header__logo {
  width: 100%;
  height: auto;
  max-width: 100px; }

.header__container {
  position: relative; }

.header__row {
  position: absolute;
  width: 100%;
  height: 100%; }

.loader-wrapper {
  width: 100%;
  height: 56px;
  position: relative; }

.loader {
  top: 50%;
  left: 50%;
  position: absolute;
  display: block;
  width: 65px;
  height: 28px;
  transform: scale(2) translate(-25%, -25%); }

.loader::before,
.loader::after {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 3px;
  height: 3px;
  background-color: #626262;
  border-radius: 50%;
  offset-path: path("M23.71 5.21l17.58 17.58a12.43 12.43 0 0 0 17.57 0 12.43 12.43 0 0 0 0-17.57 12.43 12.43 0 0 0-17.57 0L23.71 22.79a12.43 12.43 0 0 1-17.57 0 12.43 12.43 0 0 1 0-17.57 12.43 12.43 0 0 1 17.57-.01z");
  animation: loader 2000ms cubic-bezier(0.645, 0.045, 0.355, 1) infinite;
  content: ''; }

.loader::after {
  animation-delay: 200ms; }

@keyframes loader {
  0% {
    offset-distance: 0%; }
  100% {
    offset-distance: 100%; } }

.search-box {
  position: relative;
  display: flex; }

.search-box__icon {
  position: absolute;
  top: 50%;
  left: 0;
  padding-left: 10px;
  transform: translateY(-50%); }

.search-box__input {
  background-color: #f6f6f6;
  display: block;
  width: 100%;
  height: 30px;
  border-radius: 15px;
  padding-left: 40px; }
  .search-box__input:focus {
    border: 1px solid #83f3c6;
    box-shadow: 0px 0px 5px 0px rgba(131, 243, 198, 0.75); }
    .search-box__input:focus::placeholder {
      opacity: 0.1; }
  .search-box__input::placeholder {
    color: #000000;
    opacity: 0.4; }
